@use '@mview/scss/utilities/functions' as *;
@use '@mview/scss/utilities/mixins' as *;
@use 'sass:color';

.search-page-wrapper {
  padding: 0 20px;

  .basic-search-wrapper {
    width: 100%;
    position: sticky;
    top: 119px;
    background-color: pick-color('secondary', 'steam');
    z-index: 119;
    padding: 6px 0 2px 0;
  }

  .loader-wrapper {
    margin-top: 120px;
  }

  .empty-state-wrapper {
    margin-top: 120px;

    svg {
      margin-bottom: 24px;
    }
  }

  .search-results-wrapper {
    padding-bottom: 16px;

    label {
      width: 100%;
      margin-bottom: 0;
    }
  }
}
